.calendar {
  display: flex;
  flex-wrap: wrap;
  width: ($block-size + $distance) * 8;
  margin: auto;
  padding-top: 50px;

  & div {
    display: flex;
    justify-content: center;
    align-items: center;
    height: $block-size;
    width: $block-size;
    margin: $distance;
    font-family: Arial, Helvetica, sans-serif;
    font-size: 30px;
    border: 1px solid black;
    background-color: #eee;
    transition: background-color 0.5s, transform 0.5s;

    &:hover {
      transform: translate(0, -20px);
      background-color: pink;
    }
  }

  @for $i from 1 through 31 {
    & > :nth-child(#{$i})::before {
      content: "#{$i}";
    }
  }

  @each $day, $move in $days {
    &-#{$day} > :first-child {
      margin-left: ($block-size + 2 * $distance) * $move + $distance;
    }
  }

  @each $number in $numbers {
    &-#{$number} > :nth-child(n + #{($number + 1)}) {
      display: none;
    }
  }
}
